  .box {
    width: 250px;
    height: 300px;
    overflow: hidden;
    background: linear-gradient(145deg,
        #e6e6e6 0,
        #ffffff,
      );
    border-radius: 14px;
    box-shadow: 8px 8px 16px #d1d1d1,
      -8px -8px 16px #ffffff;
    padding: 50px;
    cursor: pointer;
    -webkit-animation:
      1s ease-in-out 0s 1 slideInFromRight;
            animation:
      1s ease-in-out 0s 1 slideInFromRight;
    margin: 0 auto;
    align-items: center; /* Hizalama ayarı */

  }

  @media only screen and (max-width: 1000px) {
    .box {
      width: 250px;
      height: 300px;
      padding: 5px;
      background-size: contain;
      display: block;
      margin: 40px auto;
      align-items: start;
    }
  }
  @-webkit-keyframes slideInFromRight {
    0% {
      -webkit-transform: translateX(100%);
              transform: translateX(100%);
    }
  
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
  }
  @keyframes slideInFromRight {
    0% {
      -webkit-transform: translateX(100%);
              transform: translateX(100%);
    }
  
    100% {
      -webkit-transform: translateX(0);
              transform: translateX(0);
    }
  }
  .box:hover {
    box-shadow: 0 0 20px #9ca4ab;
    -webkit-transform: translateY(-5px);
            transform: translateY(-5px);
  }
  
  .icons {
    font-size: 50px;
